import { useCallback } from 'react';
import Particles from "react-particles";
import { Link as RouterLink } from 'react-router-dom';
import { loadSlim } from "tsparticles-slim";
import { Link as ScrollLink } from 'react-scroll/modules';
import { HiChevronDown } from "react-icons/hi";


const Home = () => {
  const particlesInit = useCallback(async engine => {
    await loadSlim(engine);
}, []);

let particlesLoaded = null;

return (
    <div className='w-auto h-[calc(100vh-65px)] text-center bg-gray-900 text-zinc-200'>
        <Particles
            className='w-full h-[calc(100vh-65px)]'
            id="tsparticles"
            init={particlesInit}
            loaded={particlesLoaded}
            options={{
                fullScreen: { enable: false },
                background: {
                    color: {
                        value: "#111827",
                    },
                },
                fpsLimit: 120,
                interactivity: {
                    events: {
                        onClick: {
                            enable: true,
                            mode: "push",
                        },
                        onHover: {
                            enable: true,
                            mode: "repulse",
                        },
                        resize: true,
                    },
                    modes: {
                        push: {
                            quantity: 4,
                        },
                        repulse: {
                            distance: 100,
                            duration: 0.4,
                        },
                    },
                    detectsOn: "canvas"
                },
                particles: {
                    color: {
                        value: "#0ea5e9",
                    },
                    links: {
                        color: "#bae6fd",
                        distance: 150,
                        enable: true,
                        opacity: 0.5,
                        width: 1,
                    },
                    move: {
                        direction: "none",
                        enable: true,
                        outModes: {
                            default: "bounce",
                        },
                        random: false,
                        speed: 4,
                        straight: false,
                    },
                    number: {
                        density: {
                            enable: true,
                            area: 800,
                        },
                        value: 80,
                    },
                    opacity: {
                        value: 0.5,
                    },
                    shape: {
                        type: "circle",
                    },
                    size: {
                        value: { min: 1, max: 5 },
                    },
                },
                smooth: true, 
                detectRetina: true
            }}
        />
        <div className='relative'>
            <div className='absolute left-0 right-0 bottom-0 min-h-[calc(100vh-64px)] max-h-fit flex items-center justify-center' style={{ pointerEvents: 'none' }}>
                <div className='w-full bg-gray-900/75 border-2 border-transparent border-y-white'>
                <div className='flex flex-col justify-center items-center max-w-6xl mx-auto px-8'>
                    <h1 className='text-6xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-sky-500 to-purple-500 mt-6'
                    style={{ pointerEvents: 'auto' }}>InterEd</h1>
                    <h1 className='text-2xl text-zinc-200 mt-4' style={{ pointerEvents: 'auto' }}>
                    Your Interactive Platform for Simplified Understanding and Mastery of Computer Science Concepts.
                    </h1>
                    <RouterLink to='/courses' style={{ pointerEvents: 'auto' }}>
                    <button className='border-2 border-zinc-200 bg-gray-500/25 text-zinc-50 text-md font-bold hover:bg-purple-500 hover:border-purple-500 hover:text-gray-900
                        transition-all duration-200 px-3 py-2 flex items-center group my-6 rounded-3xl hover:scale-105'>
                        Explore Available Topics
                    </button>
                    </RouterLink>
                </div>
                </div>
            </div>
            <div className='absolute bottom-0 w-full flex items-end justify-center' style={{ pointerEvents: 'none' }}>
                <ScrollLink to='testimonial' smooth={true} duration={500} offset={-65} style={{ pointerEvents: 'auto' }}
                className='bg-gray-900/75 rounded-full border-2 border-white cursor-pointer mb-4
                hover:text-gray-900/75 hover:bg-white duration-300 ease-in'>
                    <HiChevronDown size={48} />
                </ScrollLink>
            </div>
        </div>
    </div>
  );
}


export { Home as default}